@use 'base/variables' as v;

.alerta{
   padding: 1rem 1rem 1rem 2rem;
   text-transform: uppercase;
   margin-bottom: 1rem;
   font-size: 1.2rem;
   font-weight: 900;
   max-width: 80rem;
   
   &.error {
    background-color: lighten(v.$rojoAlertas, 60);
    border-left: .5rem solid v.$rojo;
    color:v.$rojo;
    
   }

   &.exito {
      background-color: lighten(v.$verdeAlertas, 60%);
      border-left: .5rem solid v.$verde;
      color: v.$verde;
   }

}